<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">我的库存</span>
      </div>
      <div class="content-wrapper">
        <scroll class="content">
          <ul class="list">
            <li class="item" v-for="(item,index) in storeList" @click="selectItem(item)" :key="index">
              <div class="detail">
                <p class="name">{{item.productName}}</p>
                <p class="desc">{{item.productSpecs}}</p>
              </div>
              <div class="inventory">
                <div class="num">
                  <label>库存：</label>
                  <span>{{item.quantity}}</span>
                  <label class="unit">{{item.unitName}}</label>
                  <i class="forward"></i>
                </div>
              </div>
            </li>
          </ul>
        </scroll>
      </div>
      <router-view></router-view>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import {mapActions,mapGetters} from 'vuex'
  import {createProduct} from 'common/js/product'
  import {ERR_CODE} from 'api/config'
  import {getMerchantInventory} from 'api/purchase'

  export default {
    data () {
      return {

      }
    },
    created () {
      this._getMerchantInventory()
    },
    computed: {
      // 这里采用mapGetters的原因是为了解决返回不刷新数据列表的问题
      ...mapGetters([
        'storeList'
      ])
    },
    methods: {
      back() {
        this.clear()
        this.$router.back()
      },
      clear() {
        this.setCurrentProduct()//清空当前商品的缓存
      },
      selectItem(item){
        this.setCurrentProduct(item)
        this.$router.push({
          'path': `/purchase/store/${item.id}`
        })
      },
      _getMerchantInventory() {
        getMerchantInventory().then((res) => {
          if(res.errCode === ERR_CODE){
            this.setStoreList(res.data)
          }
        })
      },
      ...mapActions([
        'setCurrentProduct',
        'setStoreList'
      ])
    },
    components: {
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .slide-enter-active,.slide-leave-active
    transition all 0.3s

  .wrapper
    position absolute
    top 0
    left 0
    right 0
    bottom 0
    z-index 1
    background $color-background
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position fixed
      left 0
      right 0
      top 44px
      bottom 0
      .content
        width 100%
        height 100%
        overflow hidden
        margin-top 10px
        .list
          background #fff
          .item
            display flex
            height 73px
            justify-content space-between
            border-bottom 1px solid #E6E6E6
            box-sizing border-box
            margin 0 14px
            &:last-child
              border-bottom none
            .detail
              display flex
              flex-direction column
              color #4d4d4d
              box-sizing border-box
              padding-top 6px
              padding-bottom 6px
              .name
                font-size $font-size-medium-x
                font-weight bold
                height 28px
                display flex
                align-items center
              .desc
                font-size $font-size-small-x
                flex 1
                display flex
                align-items center
            .inventory
              font-size $font-size-medium-m
              display flex
              color #4d4d4d
              align-items flex-end
              .num
                height 24px
                margin-bottom 14px
                display flex
                align-items center
                span
                  font-size $font-size-large-m
                  color #EB4747
                  font-weight bold
                  margin-right 3px
                .forward
                  background url("~common/image/forward.png") no-repeat
                  background-size contain
                  margin-left 18px
                  width 6px
                  height 10px
                  position relative
                  &:before
                    position absolute
                    left -10px
                    top -10px
                    right -10px
                    bottom -10px
                    content ''

</style>



